import { useEffect, useState } from "react";
import styled from "styled-components";
import { movieApi } from "../../api";
import { Container } from "../Container";
import { LoaderPage } from "../Loader";
import { PageTitle } from "../PageTitle";
import { MainBanner } from "./MainBanner";
import { Movies } from "./Movies";

const Wrap = styled.div``;

const Section = styled.section`
  width: 100%;
`;

export const Home = () => {
  const [nowPlay, setNowPlay] = useState();
  const [upComming, setUpComming] = useState();
  const [topRate, setTopRate] = useState();
  const [loading, setLoading] = useState(true);
  const movieNum = 2;

  useEffect(() => {
    const movieData = async () => {
      try {
        //   console.log(await movieApi.nowPlaying());
        const {
          data: { results: nowPlaying },
        } = await movieApi.nowPlaying();
        setNowPlay(nowPlaying);

        const {
          data: { results: upComming },
        } = await movieApi.upComming();
        setUpComming(upComming);

        const {
          data: { results: topRated },
        } = await movieApi.topRated();
        setTopRate(topRated);

        setLoading(false);
      } catch (error) {
        console.log(error);
      }
    };
    movieData();
  }, []);

  // console.log("ÌòÑÏû¨ ÏÉÅÏòÅ ÏòÅÌôî", nowPlay);
  // console.log("Í∞úÎ¥â ÏòàÏ†ï ÏòÅÌôî", upComming);

  return (
    <div>
      <PageTitle title="Ìôà" />

      {loading ? (
        <LoaderPage />
      ) : (
        <>
          {nowPlay && (
            <Wrap>
              <MainBanner data={nowPlay} num={movieNum} />

              <Section>
                <Container>
                  <Movies movieData={nowPlay} title="ÌòÑÏû¨ ÏÉÅÏòÅ ÏòÅÌôî" />
                  <Movies movieData={upComming} title="Í∞úÎ¥â ÏòàÏ†ï ÏòÅÌôî" />
                  <Movies movieData={topRate} title="Ïù∏Í∏∞ ÏòÅÌôî" />
                </Container>
              </Section>
            </Wrap>
          )}
        </>
      )}
    </div>
  );
};
